@-webkit-keyframes fadeOut {
    from {opacity: 1;}
    to {opacity: 0}

}

@keyframes fadeOut {
    from {opacity: 1;}
    to {opacity: 0}

}

.successNotification {
    border-style: solid;
    border-color: black;
    border-width: 2px;
    background: green;
    padding: 10px;
    -webkit-animation-name: fadeOut;
            animation-name: fadeOut;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-delay: 2s;
            animation-delay: 2s
}

.errorNotification {
    border-style: solid;
    border-color: black;
    border-width: 2px;
    background: red;
    padding: 10px;
    -webkit-animation-name: fadeOut;
            animation-name: fadeOut;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}


